@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@200;400&display=swap);
* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #fbfbfb;
    font-family: 'IBM Plex Serif', serif;
    margin: 32px auto;
    display: flex;
    justify-content: center;
}




.blogWrapper {
    max-width: 640px;
}

h1 {
    font-size: 48px;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 30px;
}

h4 {
    font-size: 24px;
}

h5 {
    font-size: 20px;
}

h6 {
    font-size: 18px;
}

p {
    font-size: 16px;
}

h1, h2, h3, h4, h5, h6, p {
    color: #222222;
}

h1, h2, h3, h4, h5, h6 {
    margin-top: 48px;
}

p {
    margin: 24px 0;
}

code {
    background-color: #f5f5f5;
    border-radius: 4px;
    font-size: 14px;
    padding: 6px;
}

pre {
    background-color: #e9e9e9;
    border-radius: 4px;
    padding: 12px;
}

pre code {
    background-color: inherit;
}

.homeWrapper {
    min-width: 640px;
}

.homeWrapper a {
    font-size: 18px;
}

